@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(239, 239, 239);
  margin: 0 auto;
}

@media (min-width: 1024px) {
  .main-container {
    display: flex;
  }
}

.header {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 20px;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  border-bottom: 1px solid #f0f0f0;
  background-color: teal;
  transition: background-color 0.4s linear;
}

.heading {
  margin: 0;
  font-size: 20px;
  color: white;
  letter-spacing: 1px;
}

.wrapper {
  margin: 20px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  overflow: hidden;
  height: -webkit-max-content;
  height: max-content;
}

.bodyWrapper {
  padding: 20px;
}

.list {
  margin: 0;
  padding: 0;
  max-width: 500px;
  list-style: none;
  background-color: white;
  color: teal;
}

.item {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 10px;
  padding: 15px;
  align-items: center;
  line-height: 1.5;
  border-bottom: 1px solid #e1dcdc;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px 0 10px;
  background-color: crimson;
  width: 30px;
  height: 30px;
  padding: 0;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  transition: background-color 0.3s ease-in;
}

.content {
  word-break: break-word;
}

.container {
  margin: 15px;
  font-family: "Lato", sans-serif;
  color: teal;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

@media (min-width: 1024px) {
  .container {
    min-width: 300px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 20px;
  align-items: center;
}

.form-control-wrapper {
  width: 100%;
  margin-bottom: 20px;
}

.form-label {
  display: inline-block;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.form-label-padding {
  padding-left: 10px;
}

.form-control {
  display: block;
  width: 95%;
  padding: 10px;
  font-family: inherit;
  font-size: 16px;
  border: 1px solid #ddd8d8;
  border-radius: 4px;
}

.form-control-select {
  width: 100%;
}

.form-paragraph {
  font-size: 18px;
}

.submitted-footer {
  height: 50px;
  padding-top: 20px;
  background-color: #f7f7f7;
  border-top: 1px solid #f0f0f0;
}

.animation-text-onSubmit {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  color: inherit;
  font-size: 20px;
  letter-spacing: 1px;
  animation: fadeIn linear 0.3s;
  -webkit-animation: fadeIn linear 0.3s;
  -moz-animation: fadeIn linear 0.3s;
  -o-animation: fadeIn linear 0.3s;
  -ms-animation: fadeIn linear 0.3s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.form-button {
  display: block;
  margin-top: 20px;
  padding: 15px 20px;
  text-align: center;
  font-size: 16px;
  background-color: teal;
  border: 1px solid teal;
  border-radius: 4px;
  color: white;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.3s linear;
}

.form-button:hover {
  background-color: rgb(16, 160, 160);
}

